<template>
  <div>
    <el-form ref="form" :model="formData" label-width="80px">
      <el-form-item label="标题">
        <el-input v-model="formData.title"></el-input>
      </el-form-item>
      <el-form-item label="跳转链接">
        <el-input v-model="formData._id" v-if="$route.name=='轮播图添加'"></el-input>
        <el-input v-model="formData.url" v-if="$route.name=='轮播图编辑'"></el-input>
        <p style="color:#ff6700;font-size: 12px">可填写页面地址，也可填写页面id(新闻列表里)</p>
      </el-form-item>
      <el-form-item label="优先级">
        <el-input-number v-model="formData.priority" size="small" :min="1" :max="10" label="优先级"></el-input-number>
        <p style="color:#ff6700;font-size: 12px">值越大，图片显示越靠前</p>
      </el-form-item>
      <el-form-item class="load" label="状态" v-if="$route.name=='轮播图编辑'">
        <el-switch
          v-model="formData.status"
          active-color="#13ce66"
          inactive-color="#ff4949"
          active-value="1"
          inactive-value="0">
        </el-switch>
      </el-form-item>
       <el-form-item label="上传图片">
         <el-upload
           class="avatar-uploader"
           action="https://upload-z1.qiniup.com"
           :show-file-list="false"
           :on-success="upload"
           :data="{token:token}">
           <img v-if="formData.pic" :src="formData.pic" class="avatar">
           <i v-else class="el-icon-plus avatar-uploader-icon"></i>
         </el-upload>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" plain @click="submit" v-if="$route.name=='轮播图添加'">保存</el-button>
        <el-button type="primary" plain @click="change" v-if="$route.name=='轮播图编辑'">修改并保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import editor from '@/components/editor/index'

  export default {
    name: "add",
    components:{
      editor
    },
    data(){
      return{
        formData:{
          title:'',
          pic: '',
          priority: '',
          url: '',
          status: '',
          _id: ''
        },
        token:''
      }
    },
    methods:{
      upload(res){
        this.formData.pic = "http://image.yaojunrong.com/"+res.key
      },
      getToken(){
        this.$axios.qiniuGet().then(res=>{
          this.token=res.data
        })
      },
      submit(){
        this.$axios.post('addslideShows',this.formData).then(res => {
          if(res.code==200){
            this.$message({
              message: '图片添加成功',
              type: 'success'
            });
            setTimeout(()=>{
              this.$router.push('/crmslideShows')
            },1000)
          }else{
            this.$message({
              message: res.msg,
              type: 'warning'
            });
          }

        })
      },
      getdata(){
        this.$axios.post('getslideShows',{id: this.$route.query.id}).then(res=>{
          this.formData = res.data[0]
        })
      },
      change(){
        let params ={
          ...this.formData,
          id:this.$route.query.id
        }
        this.$axios.post("updateslideShows",params).then(res=>{
          if(res.code==200){
            this.$message({
              type: 'success',
              message: '修改成功!'
            });
            setTimeout(()=>{
              this.$router.push('/crmslideShows')
            },1000)
          }

        })
      }
    },
    created() {
      this.$nextTick(() => {
        if (this.$route.name == '轮播图编辑') {
          this.getdata()
        }
        this.getToken()
      })
    },
    watch:{
      $route(to,from,next){
        this.formData = {
          title: '',
          author: '',
          img: '',
          content: "",
          contentText: ''
        }
      }
    }
  }
</script>
<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .load .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
<style scoped>

</style>



